import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../service/product.service';
import { StorageService } from '../../service/storage.service';

@Component({
  selector: 'app-menu-departamentos',
  templateUrl: './menu-departamentos.component.html',
  styleUrls: ['./menu-departamentos.component.scss']
})
export class MenuDepartamentosComponent implements OnInit {

  public departaments;

  constructor(private product: ProductService, private storage: StorageService) { }

  ngOnInit() {
    this.loadDepartamens();
  }

  loadDepartamens() {

    const cache = localStorage.getItem('menu-departamento');

    if (cache) {

      const obj = JSON.parse(cache);
      this.departaments = obj.data;

    }else {

      this.storage.obterStorage('menu-departamento').subscribe(
        data => {

          if (data !== undefined) {
            this.departaments = data;
          }

        }
      );

    }

  }

}



